import { createStore, applyMiddleware } from 'redux'
import { composeWithDevTools } from 'redux-devtools-extension'

const InitialState = {
  deadline: null,
  password: null,
  oncePerIP: false,
  oncePerDay: false
}

export const actionTypes = {
  SET_DEADLINE: 'SET_DEADLINE',
  SET_PASSWORD: 'SET_PASSWORD',
  TOGGLE_ONCE_PER_IP: 'TOGGLE_ONCE_PER_IP',
  TOGGLE_ONCE_PER_DAY: 'TOGGLE_ONCE_PER_DAY'
}

// REDUCERS
export const reducer = (state = InitialState, action) => {
  switch (action.type) {
    case actionTypes.SET_DEADLINE:
      return {...state, deadline: action.value}
    case actionTypes.SET_PASSWORD:
      return {...state, password: action.value}
    case actionTypes.TOGGLE_ONCE_PER_IP:
      return {...state, oncePerIP: !state.oncePerIP}
    case actionTypes.TOGGLE_ONCE_PER_DAY:
      return {...state, oncePerDay: !state.oncePerDay}
    default: return state
  }
}

// ACTIONS
export const setDeadline = dispatch =>
  deadline => dispatch({ type: actionTypes.SET_DEADLINE, value: deadline })

export const setPassword = dispatch =>
  password => dispatch({ type: actionTypes.SET_PASSWORD, value: password })

export const toggleOncePerIP = dispatch =>
  () => dispatch({ type: actionTypes.TOGGLE_ONCE_PER_IP })

export const toggleOncePerDay = dispatch =>
  () => dispatch({ type: actionTypes.TOGGLE_ONCE_PER_DAY })

export function initializeStore (initialState = InitialState) {
  return createStore(reducer, initialState, composeWithDevTools(applyMiddleware()))
}